import React, { Component } from "react";
import FormKirimMasak from "./Component/FormKirimMasak";
import BootstrapTable from "react-bootstrap-table-next";
import FormKirimMasakBarang from "./Component/FormKirimMasakBarang";
import Swal from "sweetalert2";
import { connect } from "react-redux";
import Skeleton from "react-loading-skeleton";
import { getListKirimBahan } from "../actions/kirimBahanAction";
import { getUUID } from "../actions/fakturAction";
import { getBruto, getNetto, getQty } from "../actions/LogicAction";

const columns = [
  {
    dataField: "kodeJenis",
    text: "Kode Jenis",
    footer: "",
  },
  {
    dataField: "kadarTransaksi",
    text: "Kadar Cetak(%)",
    footer: "",
  },
  {
    dataField: "bruto",
    text: "Bruto(Gr)",
    footer: (columnData) =>
      columnData.reduce((acc, item) => parseFloat(acc) + parseFloat(item), 0),
  },
  {
    dataField: "netto",
    text: "Netto(Gr)",
    footer: (columnData) =>
      columnData.reduce((acc, item) => parseFloat(acc) + parseFloat(item), 0),
  },
];

const maptostate = (state) => {
  return {
    listBarang: state.provinsi.kirimBahan,
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    sumBruto: state.perhitungan.sumBruto,
    sumNetto: state.perhitungan.sumNetto,
    sumQty: state.perhitungan.sumQty,
  };
};

class kirimBahan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      response: "",
      tanggal: "",
      tukang: "DEFAULT|DEFAULT",
    };
  }

  componentDidMount() {
    this.props.dispatch(getListKirimBahan());
    this.props.dispatch(getUUID());
    this.props.dispatch(getBruto("kirimBahan"));
    this.props.dispatch(getNetto("kirimBahan"));
    this.props.dispatch(getQty("kirimBahan"));
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  kirimBahan() {
    if (this.state.tukang === "DEFAULT|DEFAULT") {
      Swal.fire({
        position: "top-end",
        icon: "error",
        text: "Data Harap isi Tukang",
        showConfirmButton: false,
        timer: 1500,
      });
      return false;
    } else if (this.state.tanggal === "") {
      Swal.fire({
        position: "top-end",
        icon: "error",
        text: "Data Harap isi Tanggal",
        showConfirmButton: false,
        timer: 1500,
      });
      return false;
    }
    var simpan = {
      noFaktur: this.props.noFaktur,
      noBon: this.props.noFaktur,
      tanggal: this.state.tanggal,
      kodeLokasi: "PUSAT",
      barangDetails: JSON.parse(localStorage.getItem("kirimBahan")),
      footerItem: {
        totalNetto: this.props.sumNetto,
        totalBruto: this.props.sumBruto,
        totalNettoExt: 0,
        totalP: 0,
        totalGross: 0,
        totalQty: this.props.sumQty,
        totalpkg: 0,
        totalRp: 0,
        totalK: 0,
      },
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    console.log(simpan);
  }
  handleSubmit(data) {
    var supp = data.kodeBarang
      ? data.kodeBarang
      : "DEFAULT|DEFAULT|DEFAULT|DEFAULT";
    var split = supp.split("|");
    var nama = {
      noFaktur: this.props.noFaktur,
      netto: parseFloat(data.netto),
      bruto: parseFloat(data.bruto),
      rp: 0,
      kadarTransaksi: parseFloat(data.kadarTransaksi),
      nettoDiscGlobalExt: 0,
      keterangan: split[3],
      pkg: 0,
      gross: 0,
      p: 0,
      kodeKategori: "-",
      qty: 1,
      k: 0,
      kadarCetak: 0,
      noBarcode: "-",
      kodeJenis: split[0],
      discGlobalGr: 0,
      nettoDiscGlobal: 0,
      discGlobalPr: 0,
    };
    var array = JSON.parse(localStorage.getItem("kirimBahan")) || [];
    array.push(nama);
    localStorage.setItem("kirimBahan", JSON.stringify(array));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Transaksi berhasil",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.dispatch(getListKirimBahan());
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <div className="col-lg-12">
                  <h5 className="card-title mb-3" style={{ fontSize: 30 }}>
                    Kirim Bahan
                  </h5>
                </div>
                <div className="col-lg-12">
                  <div className="row">
                    <div className="col-md-3 col-lg-3">
                      <label htmlFor="">Tanggal</label>
                      <input
                        name="tanggal"
                        id="tanggal"
                        type="date"
                        className="form-control"
                        onChange={this.inputChange("tanggal")}
                      ></input>
                    </div>
                    <div className="col-md-4 col-lg-3">
                      <label htmlFor="">Tukang</label>
                      <select
                        name="tukang"
                        id="tukang"
                        className="form-control"
                        onChange={this.inputChange("tukang")}
                      >
                        <option value="DEFAULT|DEFAULT" key="DEFAULT">
                          SILAHKAN PILIH
                        </option>
                        <option value="ANGGUN|ANGGUN" key="ANGGUN">
                          ANGGUN
                        </option>
                      </select>
                    </div>
                    <div
                      className="col-lg-1 col-md-1 no-gutters form-group"
                      style={{ marginLeft: -30 }}
                    >
                      <label htmlFor="" className="text-white">
                        .
                      </label>
                      <button
                        className="btn btn-primary form-control"
                        data-toggle="modal"
                        data-target="#primary-header-modal"
                      >
                        +
                      </button>
                    </div>
                    <div className="col-md-5 col-lg-5"></div>
                  </div>
                  <FormKirimMasakBarang
                    onSubmit={(data) => this.handleSubmit(data)}
                  />
                </div>
              </div>
              <div className="col-lg-12">
                <div className="col-lg-12 col-md-12 mb-2">
                  <div className="text-left">
                    <button
                      className="btn btn-primary"
                      onClick={() => this.kirimBahan()}
                    >
                      Simpan Data
                    </button>
                  </div>
                </div>
              </div>
              <div className="col-lg-12">
                <div className="col-lg-12">
                  {this.props.listBarang ? (
                    <BootstrapTable
                      keyField="id"
                      data={this.props.listBarang}
                      columns={columns}
                      bordered={true}
                      headerClasses="header-table"
                    />
                  ) : (
                    <Skeleton width={"100%"} height={100} />
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tukang Masak
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormKirimMasak />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(kirimBahan);
